export default function time(setTime){
    var set = setTime.substring(0, setTime.indexOf("."))       
    var set = new Date(set)
    var now = new Date()
    var s = parseInt((now - set)/1000)

    var year =parseInt( s/(60*60*24*365))
    var mouth =parseInt( s /(60*60*24*30))
    var date =parseInt( s /(60*60*24))
    var hour =parseInt( s / (60*60))
    var mintes =parseInt( s / 60)
    var seonds = parseInt(s)
    if(year>0){
        return year + '年前'
    }else if(mouth > 0){
        return mouth + "月前"
    }else if(date > 0){
        return date + '天前'
    }else if(hour > 0){
        return hour + '小时前'
    }else if(mintes >0){
        return mintes + '分钟前'
    }else if(seonds >0){
        return seonds + '秒前'
    }
}