<template>
  <div class="home" v-if="datalist">
    <div class="left">
      <div class="title">
        <ul class="ul">
          <router-link
            tag="li"
            v-for="item in tablist"
            :key="item.tabs"
            :class="{ colorA: tabs == item.tabs }"
            @click.native="getA"
            :to="{ name: 'homeshow', query: { tab: item.tabs } }"
          >
            {{ item.con }}
          </router-link>
        </ul>
      </div>
      <div class="t-con">
        <ul>
          <li v-for="data in datalist" :key="data.id" @click="push(data.id)">
            <div class="t-left">
              <div class="list-img">
                <img :src="data.author.avatar_url" alt="" />
              </div>
              <div class="pin">
                <span>{{ data.reply_count }}</span
                >/<span>{{ data.visit_count }}</span>
              </div>
              <div class="btn" :class="{ activecolor: data.top }">
                {{ data.tab | china(data.top) }}
              </div>
              <div class="p">{{ data.title }}</div>
            </div>
            <div class="t-right">
              <div class="sm"><img :src="data.author.avatar_url" alt="" /></div>
              <p>{{ data.create_at | Time }}</p>
            </div>
          </li>
        </ul>
      </div>
    </div>
    <div class="right">
      <User></User>
    </div>
  </div>
</template>

<script>
import User from "@/components/User.vue";

import time from "@/assets/js/time.js";

export default {
  data() {
    return {
      datalist: [],
      tabs: "",

      tablist: [
        {
          tabs: "all",
          con: "全部",
        },
        {
          tabs: "good",
          con: "精华",
        },
        {
          tabs: "share",
          con: "分享",
        },
        {
          tabs: "ask",
          con: "问答",
        },
        {
          tabs: "job",
          con: "招聘",
        },
        {
          tabs: "dev",
          con: "测试",
        },
      ],
    };
  },
  components: {
    User,
  },
  methods: {
    getA() {
      this.getAjax();
    },
    Create() {
      this.$router.push({
        name: "create",
      });
    },
    push(id) {
      this.$router.push({
        name: "homedetail",
        params: {
          id: id,
        },
      });
    },
    getAjax() {
      var url = new URL("https://cnodejs.org/api/v1/topics");
      var params = {
        tab: this.tabs,
      };
      url.search = new URLSearchParams(params).toString();
      fetch(url)
        .then((res) => res.json())
        .then((res) => {
          this.datalist = res.data;
        });
    },
  },
  beforeMount() {
    this.getAjax();
  },
  filters: {
    Time: function(val) {
      return time(val);
    },
    china(val, top) {
      if (top == true) {
        return "置顶";
      } else if (val == "ask") {
        return "问答";
        // return this.tablist[this.tablist.findIndex(item => item.tabs === val)].con
      } else if (val == "job") {
        return "招聘";
      } else if (val == "share") {
        return "分享";
      } else if (val == "dev") {
        return "测试";
      } else {
        return "精华";
      }
    },
  },
  watch: {
    $route: {
      immediate: true,

      handler(value) {
        if (this.$route.query.tab) {
          this.tabs = this.$route.query.tab;
          return this.$route.query.tab === value;
        } else {
          return (this.$route.query.tab = "all");
        }
      },
    },
  },
};
</script>

<style lang="scss" scoped>
img {
  width: 100%;
  display: block;
}
.home {
  width: 100%;
  display: flex;
  justify-content: space-between;
  .left {
    width: 1080px;
    .ul {
      padding-top: 10px;
      display: flex;
      li {
        cursor: pointer;
        padding: 0px 3px;
        margin: 0 10px;
        height: 18px;
        font-size: 12px;
        border-radius: 3px;
        color: #86c00e;
      }
      .router-link-exact-active {
        color: white;
        background: #86c00e;
      }
    }
    .t-con {
      li {
        cursor: pointer;
        display: flex;
        justify-content: space-between;
        align-items: center;
        border-bottom: 1px solid #f0f0f0;
        padding: 5px 10px;
        box-sizing: border-box;
        .t-right {
          width: 100px;
          display: flex;
          justify-content: space-between;
          align-items: center;
          .sm {
            width: 30px;
            border-radius: 4px;
            overflow: hidden;
          }
        }
        .t-left {
          flex: 1;
          display: flex;
          align-items: center;
          .list-img {
            width: 40px;
            border-radius: 4px;
            overflow: hidden;
          }
          .pin {
            text-align: center;
            width: 100px;
            span {
              &:nth-child(1) {
                font-size: 14px;
              }
              &:last-child {
                font-size: 12px;
              }
            }
          }
          .btn {
            margin: 0 5px;
            border-radius: 5px;
            font-size: 12px;
            padding: 3px 6px;
            background: #e5e5e5;
            color: #999999;
          }
          .activecolor {
            color: white;
            background: #80bd01;
          }
        }
      }
    }
  }
  .right {
    width: 308px;
    overflow: hidden;
    section {
      margin-bottom: 12px;
      .title {
        line-height: 36px;
        padding-left: 10px;
        font-size: 12px;
      }
      .t-con {
        padding-left: 10px;
        .t-c-top {
          display: flex;
          align-items: center;
          .timg {
            width: 50px;
            border-radius: 3px;
            overflow: hidden;
            margin-right: 10px;
          }
          .actour {
            font-size: 14px;
            color: #a08990;
          }
        }
        .t-c-cen {
          padding: 10px 0;
          font-size: 12px;
        }
        .t-c-bot {
          font-style: italic;
          font-size: 14px;
        }
      }
    }
    .btn {
      .button {
        margin-left: 10px;
        display: inline-block;
        padding: 5px 10px;
        font-size: 18px;
        background: #80bd01;
        color: white;
        border-radius: 4px;
      }
    }
  }
  .title {
    padding: 0 10px;
    height: 36px;

    background: #f6f6f6;
  }
  .t-con {
    padding: 10px 0;
    background: #ffffff;
  }
}
</style>
