<template>
  <div class="register">
    <div class="left">
      <div class="title"><span @click="to">主页</span>/ <span>登录</span></div>
      <div class="t-con">
        <div class="c-left">
          <p>用户名</p>
          <p>密码</p>
        </div>
        <div class="c-right">
          <div class="p"><input type="text" /></div>
          <div class="p">
            <input type="text" v-model="accesstoken" placeholder="请输入密码" />
          </div>
          <div class="btn-wrap">
            <span @click="login">登录</span>
            <span>通过GitHub登录</span>
            <span>忘记密码了？</span>
          </div>
        </div>
      </div>
    </div>
    <div class="right">
      <div class="title">
        关于
      </div>
      <div class="t-con">
        <p>CNode:Node.js专业中文社区</p>
        <p>在这里你可以</p>
        <ul>
          <li>向别人提出你遇到的问题</li>
          <li>帮助遇到问题的人</li>
          <li>分享自己的知识</li>
          <li>和其它人一起进步</li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
import EventBus from "../assets/bus";
export default {
  data() {
    return {
      accesstoken: "",
      userInfo: null,
    };
  },
  methods: {
    to() {
      this.$router.push({
        name: "homeshow",
      });
    },
    login() {
      fetch(
        "https://cnodejs.org/api/v1/accesstoken?accesstoken=" +
          this.accesstoken,
        {
          method: "POST",
        }
      )
        .then((res) => res.json())
        .then((res) => {
          console.log(res);

          if (res.success) {
            this.userInfo = res;
            localStorage.setItem("token", this.accesstoken);
            this.$router.replace({
              name: "homeshow",
            });
            EventBus.$emit("handleStorage", this.accesstoken);
          }
        });
    },
  },
};
</script>

