<template>
     <div class="update2">
        <div class="left">
             <div class="title">
                 <span>主页</span>/ <span>编辑话题</span>
            </div> 
            <div class="t-con">
                <div class="select">
                    <span>选择模块：</span>
                        <div class="option">
                                 <select name="" v-model="sel" id="">
                                    <option disabled value="">请选择</option>
                                    <option value="ask">问答</option>
                                    <option value="share">分享</option>
                                    <option value="job">招聘</option>
                                    <option value="dev">测试</option>
                                  </select>
                        </div>
                       
                </div>
                <div class="titleContent">
                    <input type="text"  v-model="title" placeholder="标题数字10字以上">
                </div>
             
                <div class="v-content">
                    <textarea  v-model="text">
                           
                    </textarea>
                </div>
                <div class="post">
                    <div class="post-btn" @click="postAjax">提交</div>
                </div>
            </div>
         </div>  
          <div class="right">
                 <section class="first">
                     <div class="title">Markdown 语法参考</div>
                     <div class="t-con">
                        <ul>
                            <li>### 单行的标题</li>
                            <li>** 粗体 **</li>
                            <li>`console.log("行内代码")`</li>
                            <li>```js\n code \n```标记代码块</li>
                            <li>[内容](链接)</li>
                            <li>![文字说明](图片链接)</li>
                            <li>Markdown文档</li>
                        </ul>
                     </div>
                 </section>
                <section>
                    <div class="title">话题发布指南</div>
                    <div class="t-con">
                            <ul>
                                <li>尽量把话题要点浓缩到标题里</li>
