                            </ul>
                    </div>
                </section>
               
                
          </div>
     </div>
</template>

<script>
import axios from "axios"
    export default {
          props:["id"],
           data() {
            return {
                sel: "",
                title:"",
                text:"",
               
            }
        },
        methods: {
            // 修改数据
              postAjax(){
                     var url="https://cnodejs.org/api/v1/topics"
                    var data = {
                            accesstoken:"f27be2e8-623d-4530-bba2-77dbdb9f7303",
                            title:this.title,
                            tab:this.sel,
                            content:this.text
                    }
                    
                    fetch(url,{
                        method:'POST',
                        body: JSON.stringify(data),
                        headers: new Headers({
                            'Content-Type': 'application/json'
                        })
                    }).then(res => res.json()).then(res =>{
                        console.log(res);
                            this.$router.push({
                                name:"update",
                                params:{
                                    id:res.topic_id
                                }
                            })
                        
                     })
            },
            
        },
        mounted(){
            // 先获取数据 渲染  然后进修改
               axios({
                    url: 'https://cnodejs.org/api/v1/topic/' + this.id   ,
                    params:{
                        mdrender:"true"
                    }
                    }).then(res => {
                        this.val = res.data.data
                       this.sel=this.val.tab
                        this.title = this.val.title
                        this.text  = this.val.content
                        console.log(this.text);
                    })
        }
    }
</script>

