import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '@/views/Home.vue';
import Newer from '@/views/Newer.vue';
import News from '@/views/News.vue';
import API from '@/views/API.vue';
import About from '@/views/About.vue';
import Login from '@/views/Login.vue';
import Register from '@/views/Register.vue';
import Homeshow from "@/views/Home/Homeshow.vue"
import Update from "@/views/Home/Update.vue"
import Update2 from "@/views/Home/Update2.vue"
import Remove from "@/views/Home/Remove.vue"
import Create from "@/views/Home/Create.vue"
import Homedetail from "@/views/Home/Homedetail.vue"
import Collect from "@/views/Home/Collect.vue"
import Userlogin from '@/views/User/Userlogin'
Vue.use(VueRouter)

const routes = [
    {
      path:'/home',
      name:"home",
      component:Home,
      redirect:{
        name:"homeshow"
      },
      children:[
        {
          path:'create',
          name:"create",
          props:true,
          component:Create
        },
        {
          path:'userlogin',
          name:"userlogin",
          props:true,
          component:Userlogin
        },
        {
          path:'remove',
          name:"remove",
          props:true,
          component:Remove
        },
        {
          path:'collect',
          name:"collect",
          props:true,
          component:Collect
        },
        {
          path:'update/:id',
          name:"update",
          props:true,
          component:Update
        },
        {
          path:'update2/:id',
          name:"update2",
          props:true,
          component:Update2
        },
        {
          path:'homeshow',
          name:"homeshow",
          component:Homeshow,
        },
        {
          path:'homedetail/:id',
          name:"homedetail",
          props:true,
          component:Homedetail,
        },
      ]
    },
    {
      path:'/newer',
      name:"newer",
      component:Newer
    },
    {
      path:'/news',
      name:"news",
      component:News
    },
    {
      path:'/api',
      name:"api",
      component:API
    },
    {
      path:'/about',
      name:"about",
      component:About
    },
    {
      path:'/login',
      name:"login",
      component:Login
    },
    {
      path:'/register',
      name:"register",
      component:Register
    },
    {
      path:"/",
      redirect:{
        name:"home"
      }
    }
  
]

const router = new VueRouter({
  routes
})

export default router
