<template>
      <div class="Collect" >
        <div class="left">
               <div class="title">
                 <span>主页</span>/ <span>全部收藏</span>
             </div> 
             <div class="t-con">
                   <ul>
                         <li v-for="data in datalist" :key="data.id" @click="push(data.id)">
                             <div class="t-left">
                                    <div class="list-img"><img :src="data.author.avatar_url" alt=""></div>
                                    <div class="pin"><span>{{data.reply_count}}</span>/<span>{{data.visit_count}}</span></div>
                                    <div class="btn" :class="{ activecolor: data.top}">置顶</div>
                                    <div class="p">{{data.title}}</div>
                             </div>
                             <div class="t-right">
                                 <div class="sm"><img :src="data.author.avatar_url" alt=""></div>
                                 <p>{{data.create_at | Time }}</p>
                             </div>
                         </li>
                       
                     </ul>
             </div>
               
         </div>  
          <div class="right">
                 <User></User>
          </div>
     </div>
</template>

<script>
import axios from 'axios'
import User from '@/components/User.vue';
import time from "@/assets/js/time.js"
export default {
    data() {
        return {
            val: "",
            datalist:[]
        }
    },
    components: {
        User
    },
     filters: {
            Time: function(val) {
              return  time(val)
            }
        },
        methods:{
             push(id){
                this.$router.push({
                    name:'homedetail',
                    params:{
                        id:id
                    }
                })
            }
        },
    mounted () {
         axios({
            url: 'https://cnodejs.org/api/v1/topic_collect/QuerySelector123'    ,
            params:{
                mdrender:"true"
            }
            }).then(res => {
                this.datalist = res.data.data
               
            
            })
    },
};
 
</script>

<style lang="scss" scoped>
img{
    width: 100%;
    display: block;
}
         .Collect{
            width: 100%;
            display: flex;
            justify-content: space-between;
            .title{
                padding:0 10px ;
                height: 36px;
                background: #f6f6f6;
            }
            .t-con{
                padding: 10px ;
                background: #ffffff;
            }
            .t-con2{
                padding: 10px 0;
            }
            .left{
                width: 1080px;
                 .title{
                    line-height: 36px;
                    font-size: 14px;
                    span{
                        &:nth-child(1){
                            color: #80bd01;
                        }
                    }
                    }
                   .t-con{
                    ul{
                        li{
                            cursor: pointer;
                            box-sizing: border-box;
                           padding: 0 10px;
                          display: flex;
                          align-items: center;
                          justify-content: space-between;
                          height: 50px;
                          border-bottom: 1px solid #f4f4f4;
                          .t-right{
                              width: 76px;
                              display: flex;
                              justify-content: space-between;
                              align-items: center;
                              .sm{
                                  width: 20px;
                                  border-radius: 3px;
                                  overflow: hidden;
                              }
                              p{
                                  font-size: 10px;
                                 
                              }
                          }
                          .t-left{
                              display: flex;
                              align-items: center;
                              .list-img{
                                  width: 30px;
                                  overflow: hidden;
                                  border-radius: 3px;
                                  margin-right:10px ;
                              }
                              .pin{
                                  width: 67px;
                                  span{
                                      &:nth-child(1){
                                          font-size: 12px;
                                      }
                                      &:nth-of-type(2){
                                          font-size: 10px;
                                      }
                                  }
                              }
                             
                              .btn{
                                  padding:1px 3px ;
                                  color:  #80bd01;
                                  font-size: 12px;
                                  border-radius: 2px;
                                  overflow: hidden;
                              }
                               .activecolor{
                              color:white;
                                   background: #80bd01;
                              }
                              .p{
                                  margin-left: 5px;
                                  font-size: 14px;
                                  overflow: hidden;
                              }
                          }
                        }
                    }
                }   
               
            }

            .right{
                width: 308px;
                overflow: hidden;
               
            }
            
        }
</style>