<template>
     <div class="update" v-if="val">
        <div class="left">
               <div class="t-con t-con2">
                   <div class="t-c-top">
                        <div class="t-left">
                                <h1>{{val.title}}</h1>
                                <p>•<span>发布于几秒前</span>作者
                                <span>{{val.author.loginname}}</span> 
                                <span>{{val.visit_count}}</span>次浏览• 来自 
                                <span>{{val.tab}}</span>
                                </p>
                                <div class="font">
                                    <span class="iconfont icon-xiugai-copy" @click="update"></span>
                                    <span class="iconfont icon-shanchu" @click="del"></span>
                                </div>
                        </div>
                        <div class="t-right">
                            <div class="btn" :class="{'act':ac}" ref="mycoll"
                             @click="collect()"
                             >收藏</div>
                        </div>
                   </div>
                    <div class="t-c-bot">
                        <div v-html="val.content"></div>
                    </div>
               </div>
               <div class="t-home">
                   <div class="title">添加回复</div>
                    <div class="t-c-cont">
                        <textarea v-model="text">  </textarea>
                    </div>
                    <div class="t-btn">
                        <div class="btn" @click="hand(val.replies)">回复</div>
                    </div>
               </div>
         </div>  
          <div class="right">
                 <section class="first">
                     <div class="title">Markdown 语法参考</div>
                     <div class="t-con">
                        <ul>
                            <li>### 单行的标题</li>
                            <li>** 粗体 **</li>
                            <li>`console.log("行内代码")`</li>
                            <li>```js\n code \n```标记代码块</li>
                            <li>[内容](链接)</li>
                            <li>![文字说明](图片链接)</li>
                            <li>Markdown文档</li>
                        </ul>
                     </div>
                 </section>
                <section>
                    <div class="title">话题发布指南</div>
                    <div class="t-con">
                            <ul>
                                <li>尽量把话题要点浓缩到标题里</li>
                                <li>代码含义和报错可在 SegmentFault 提问</li>
                            </ul>
                    </div>
                </section>
               
                
          </div>
     </div>
</template>

<script>
import axios from "axios"
export default {
    props:["id"],
    data() {
        return {
            val:"",
            text:"",
            ac:false
        }
    },
    methods: {
        update(){
            this.$router.push({
                name:"update2",
                params:{
                    id:this.id
                }
            })
        },
        del(){
            let con =confirm("你确定要删除当前会话吗")
            if(con){
                localStorage.removeItem(this.id)
                this.$router.push({
                    name:"remove"
                })
            }else{
               return 
            }
           
        },
        collect(){
           
            this.val.iscoll = !this.val.iscoll 
            if( this.val.iscoll){
                 this.$refs.mycoll.innerText = "取消收藏"
                  this.ac = true
                  this.updategood()
            }else{
                 this.$refs.mycoll.innerText = "收藏"
                  this.ac = false
            }
            

          
            

        },
          hand(arr){
              return arr.push(this.text)
          },
        //   收藏上传主题
          updategood(){
                     var url="https://cnodejs.org/topic/collect"
                    var data = {
                            title:this.val.title,
                            tab:this.val.tab,
                            content:this.val.content,
                             accesstoken:" be6fb4ea-5d8f-4c40-9936-e4ad9b972b03",
                            topic_id:this.id
                    }
                    
                    fetch(url,{
                        method:'POST',
                        body: JSON.stringify(data),
                        headers: new Headers({
                            'Content-Type': 'application/json',
                        })
                    }).then(res => res.json()).then(res =>{
                            console.log(res);
                     })
            },
          
    },
    mounted () {
          axios({
            url: 'https://cnodejs.org/api/v1/topic/' + this.id   ,
            params:{
                mdrender:"true"
            }
            }).then(res => {
                this.val = res.data.data
                
            
            })
    },
}
</script>
<style  lang="scss" scoped>
         .update{
            width: 100%;
            display: flex;
            justify-content: space-between;
            .title{
                padding:0 10px ;
                height: 36px;
                
                background: #f6f6f6;
            }
            .t-con{
                padding: 10px ;
                background: #ffffff;
            }
            .t-con2{
                padding: 10px 0;
            }
            .left{
                width: 1080px;
                .t-con{
                    margin-bottom: 10px;
                    .t-c-top{
                        box-sizing: border-box;
                        padding: 0 10px;
                        border-bottom: 1px solid #ccc;
                        display: flex;
                        justify-content: space-between;
                        align-items: center;
                        .t-left{
                            p{
                                font-size: 14px;
                                color: #9a95b4;
                                padding: 10px 0;
                            }
                            .font{
                                margin-bottom: 10px;
                                font-size: 20px;
                                color:#9a95b4;
                                .iconfont{
                                    margin-right: 10px;
                                }
                            }
                        }
                        .t-right{
                            .btn{
                                cursor: pointer;
                                padding:6px 12px ;
                                font-size: 14px;
                                color: white;
                                background: #80bd01;
                                border-radius: 4px;
                                &:hover{
                                    opacity: 0.8;
                                }
                            }
                            .act{
                                color: #333;
                                background:#e5e5e5 ;
                                &:hover{
                                    background: #909090;
                                    color: white;
                                }
                            }
                        }
                    }
                    .t-c-bot{
                        font-size: 14px;
                        padding: 10px;

                    }
                }
                .t-home{
                    background: white;
                   
                    .title{
                        font-size: 14px;
                        padding: 0 10px;
                        line-height: 36px;
                    }
                    .t-c-cont{
                         height: 300px;
                        display: flex;
                        textarea{
                            padding: 10px;
                            font-size: 14px;
                            flex: 1;
                            border: none;
                            outline: none;
                        }

                    }
                    .t-btn{
                        padding: 10px;
                        .btn{
                            cursor: pointer;
                            display: inline-block;
                            padding: 6px 12px;
                            font-size: 14px;
                            color: white;
                            background: #0088cc;
                        }
                    }
                }
              
            }

            .right{
                width: 308px;
                section{
                    margin-bottom: 12px;
                    .title{
                        line-height: 36px;
                        padding-left: 10px;
                        font-size: 12px;
                    }
                    .t-con{
                        .select{
                            font-size: 14px;
                        }
                        ul{
                            li{
                                font-size: 14px;
                                line-height: 36px;
                            }
                        }
                    }
                   
                   
                }
               
            }
            
        }
</style>