<template>
     <div class="userlogin" >
          <div class="left">
              <div class="first">
                   <div class="title">
                         <span>主页</span>/
                    </div>
                    <div class="t-con" >
                                <div class="t-c-top">
                                        <div class="timg">
                                        <img :src="val.avatar_url" alt="">
                                        </div>
                                    <div class="actour">{{val.loginname}}</div>
                                </div>
                                <div class="t-c-cen">
                                    <p> <span>{{val.score}}</span>积分 </p>
                                </div>
                                <p class="por" @click="pucoll"><span >{{datalist.length}}</span>个话题收藏</p>
                                <p>@{{val.loginname}}</p>
                                <p >注册时间<span>{{ct}}</span></p>
                    </div>
              </div>
              <div class="seconds">
                  <div class="title">最近创建的话题</div>
                  <div class="t-con" v-for="(data,index) in datalist" :key="data.id"  >
                      <div class="t-left"  v-if="datalist">
                           <div class="t-img"><img :src="data.author.avatar_url" alt=""></div>
                            <div class="t-pic"><span>0</span>/ <span>1</span></div>
                            <div class="p por" @click="pudate(data.id)">{{data.title}}</div>
                      </div>
                     <div class="t-right">
                        <div class="t-time">{{ datalist | ctime(index)}}</div>
                     </div>
                  </div>
                     <div class="btnn">查看更多》</div> 
              </div>
             
               <div class="bottom">
                   <div class="title">最近参与的话题</div>
                   <div class="t-con">
                       <div class="html">无话题</div>
                   </div>
               </div>
         </div>  
          <div class="right">
                 <User></User>
          </div>
    </div>
</template>

<script>
import axios from 'axios'
import User from '@/components/User.vue'
import time from "@/assets/js/time.js"
    export default {
        data() {
            return {
                val: "",
                datalist:[],
                time:''
            }
        },
        components: {
            User,
        },
        methods:  {
            pucoll(){
                this.$router.push({
                    name:"collect"
                })
            },
            pudate(id){
                this.$router.push({
                    name:"update",
                    params:{
                        id:id
                    }
                })
            }
        },
        mounted(){
                 axios({
                    url: 'https://cnodejs.org/api/v1/user/QuerySelector123' 
                   
                }).then(res => {
                    this.val = res.data.data
                 
                    this.datalist = this.val.recent_topics
                   
                    
                })
         },
        
         computed:{
             ct() {
                 var t = this.val.create_at
                return time(t)
             }
                
         },
         filters: {
             ctime: function(value,index) {
              var   t = value[index].last_reply_at
                 return time(t)
             },
            
         }
    }
</script>

<style lang="scss" scoped>
img{
    width: 100%;
    display: block;
}           
.por{
    cursor: pointer;
}
         .userlogin{
            width: 100%;
            display: flex;
            justify-content: space-between;
            .title{
                padding:0 10px ;
                height: 36px;
                background: #f6f6f6;
                 line-height: 36px;
            }
            .t-con{
                padding: 10px 0;
                background: #ffffff;
            }
            .left{
                width: 1080px;
                &>div{
                    margin-bottom: 10px;
                }
                .first{
                    .title{
                        span{
                            &:nth-child(1){
                                line-height: 36px;
                                font-size: 14px;
                                color: #aace68;
                            }
                        }
                    }
                    .t-con{
                        font-size: 14px;
                        padding:  10px;
                        .t-c-top{
                            display: flex;
                            align-items: center;
                            .timg{
                                width: 50px;
                                border-radius: 4px;
                                overflow: hidden;
                                margin-right: 10px;
                            }
                            .actour{
                                font-size: 14px;
                                color: #848792;
                            }
                            
                        }
                        .t-c-cen{
                             p{
                                 color: #333;
                                 span{
                                     
                                     margin-right: 5px;
                                 }
                             }
                        }
                        p{
                            height: 40px;
                            line-height: 40px;
                              color: #848792;
                        }
                    }
                }
                .seconds{
                    font-size: 14px;
                    .title{
                        line-height: 36px;
                    }
                    .t-con{
                        display: flex;
                        align-items: center;
                        justify-content: space-between;
                        border-bottom: 1px solid #f5f5f5;
                        box-sizing: border-box;
                        padding: 10px;
                        .t-right{
                            width: 80px;
                            text-align: right;
                        }
                        .t-left{
                            display: flex;
                            align-items: center;
                            flex: 1;
                            .t-img{
                                width: 40px;
                                border-radius: 4px;
                                overflow: hidden;
                                margin-right: 10px;
                            }
                            .t-pic{
                                text-align: center;
                                span{
                                    color:#a188ca ;
                                    &:nth-child(1){
                                        font-size: 14px;
                                    }
                                    &:last-child{
                                        font-size: 12px;
                                    }
                                }
                            }
                            .p{
                                margin-left: 10px;
                                font-size: 16px;
                                color: #1893d1;
                            }
                        }

                    }
                    .btnn{
                        background: white;
                        height: 36px;
                        line-height: 36px;
                       padding: 0 10px;
                    }
                }
                .bottom{
                    font-size: 14px;
                    .html{
                        padding: 0 10px 20px;
                    }
                }
              
            }
            .right{
                width: 308px;
                overflow: hidden;
            }
            
        }
</style>